<template>
  <div class="musica">
      <BaseMainBlock>
          <template #header>
              <h1>M U S I C</h1> 
          </template>
          <template #default>
              <ul>
                  <li v-for="artist in artists" :key="artist.id">{{ artist.nickname }}</li>
              </ul>
          </template>
      </BaseMainBlock>
  </div>
</template>

<script>
import BaseMainBlock from '@/components/BaseMainBlock.vue';
import { getArtists } from '@/utils/api.js'; // Importa la funzione per ottenere gli artisti

export default {
  components: {
      BaseMainBlock
  },
  data() {
      return {
          artists: []
      };
  },
  methods: {
      async fetchArtists() {
          this.artists = await getArtists();
      }
  },
  mounted() {
      this.fetchArtists(); // Recupera gli artisti quando il componente è montato
  }
};
</script>

  <style scoped>
  /* Stili specifici per la pagina Musica */
  </style>
  